@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
/*Utility class*/
.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.card {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-image: linear-gradient(
    to right top,
    #8492ed,
    #c176d8,
    #ed52a8,
    #fd3a65,
    #eb4b12
  );
  /* padding: 10px 25px;
  border-radius: 20px; */
  padding: 20px 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  min-height: 100vh;
  background-image: linear-gradient(
    to right top,
    #e956aa,
    #fc5d77,
    #f3784a,
    #d5962c,
    #aab034,
    #79bd61,
    #44c493,
    #00c7bf,
    #00bfe6,
    #00b1ff,
    #879afe,
    #d17ada
  );
  color: white;
}

.container {
  max-width: 100%;
  margin: 0 auto;
}

img {
  width: 100%;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

button {
  border: 0;
}

button:hover {
  cursor: pointer;
}

input {
  border: 0;
}
button:focus,
input:focus {
  outline: 0;
}

/* Hamburger Button */
.menu {
  font-size: 2em;
  position: absolute;
  top: 30px;
  left: 50px;
  color: black;
  cursor: pointer;
  -webkit-animation: shrink-grow 2s infinite linear;
          animation: shrink-grow 2s infinite linear;
}

/* Side Bar */
.sidebar {
  background-image: linear-gradient(
    to right top,
    #be5691,
    #ba62a5,
    #b26eb8,
    #a87bc9,
    #9b87d8,
    #a787d9,
    #b288d9,
    #bd88d9,
    #de7cc3,
    #f473a5,
    #fe7282,
    #fb7a5f
  );
  width: 430px;
  top: 0;
  left: 0;
  height: 100%;
  position: fixed;
  transition: all 1s ease;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.show-sidebar {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.hide-sidebar {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

/* Sidebar Header */
.header {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.hLeft span {
  font-size: 1.2em;
  letter-spacing: 2px;
}
.hRight {
  cursor: pointer;
}

/* Sidebar Links */
.links {
  margin-top: 50px;
  padding: 0 0 0 10px;
}
.link {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 15px 0;
  cursor: pointer;
}
.link span:first-child {
  font-size: 2.3em;
  margin-right: 20px;
}

.link span:nth-child(2) {
  font-size: 1.5em;
  text-transform: capitalize;
  letter-spacing: 2px;
}

/* Side bar Social-links */
.social-links {
  max-width: 80%;
  margin: 0 auto;
  margin-top: 140px;
  font-size: 1.5em;
}
.social-links > * {
  color: #140411;
  padding: 0 15px 0;
}

/* Login Button */
.login {
  font-size: 1.5em;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 10px 20px;
  font-weight: 400;
  transition: all 0.32s ease;
  background: #ee61b1;
  color: white;
}
.login:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  background: #79bd61;
  color: #140411;
  border: 2px solid #00b1ff;
}

/* Login Modal */
.login-modal-bg {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  /* visibility: hidden; */
  z-index: 10;
}
.show-login-modal {
  visibility: visible;
}

.login-modal {
  width: 40%;
  height: 300px;
  background: white;
  position: relative;
  border-radius: 5px;
  padding: 10px 20px;
}

input {
  font-size: 1.2em;
  margin-bottom: 20px;
  padding: 10px 20px;
  border: 1px solid #888;
  width: 100%;
  text-align: center;
}
.login-form button {
  font-size: 1.3em;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 5px 20px;
  font-weight: 400;
  transition: -webkit-transform 0.32s ease;
  transition: transform 0.32s ease;
  transition: transform 0.32s ease, -webkit-transform 0.32s ease;
  background: #ee61b1;
  color: white;
  width: 100%;
  margin-top: 10px;
}
.login-form button:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  background: #79bd61;
  color: #140411;
  border: 2px solid #00b1ff;
}

/* Close Modal Button */
.close-modal {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 1.3em;
  color: #f33c3c;
  cursor: pointer;
}
@media (max-width: 850px) {
  .sidebar {
    width: 100%;
  }
}

@-webkit-keyframes shrink-grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shrink-grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/* Media Queries for mobile View */
@media (max-width: 600px) {
  .login-modal {
    width: 80%;
    height: 280px;
    border-radius: 0px;
    padding: 12px 20px 0px;
  }

  input {
    font-size: 0.9em;
    margin-bottom: 20px;
    padding: 8px 0px;
    border: 1px solid #999;
  }
  .login-form button {
    font-size: 1em;
    text-transform: uppercase;
    letter-spacing: 4px;
    padding: 8px 0px;
    margin-top: 16px;
  }
}

